import React,{Component} from 'react'
import { hashHistory} from 'react-router';
import { Form, Input, Button, Radio, Spin, message } from 'antd';
const FormItem = Form.Item;
const { TextArea } = Input;
export class DataForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            "name": "",
            "email": "",
            "mobile": "",
            "content": "",
            "time": "",
            loading: true
        };
    }

    componentDidMount() {
        let that = this;
        ajax({
            url: "/help/feedback/getInfoDetail",
            data: {
                id: this.props.id
            },
            callback: function(data) {
                if(data.status == 200) {
                    let { name, email, mobile, time, content } = data.data.detailInfo;
                    that.setState({
                        loading: false,
                        name,
                        email,
                        mobile,
                        time,
                        content
                    });
                    that.KE = K.create('#content', {
                        allowFileManager : true,
                        afterChange: function() {
                            that.props.form.setFieldsValue({"content" :this.html()});
                        }
                    });
                    that.KE.html(content);
                    that.KE.readonly();
                    $(".anticon-delete").remove();
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        });
    }

    returnList() {
        hashHistory.push("/help/feedback");
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 6 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        {...formItemLayout}
                        label="姓名"
                    >
                        {getFieldDecorator('name', {
                            initialValue: this.state.name
                        })(
                            <Input disabled />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="电话"
                    >
                        {getFieldDecorator('mobile', {
                            initialValue: this.state.mobile
                        })(
                            <Input disabled />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="邮箱"
                    >
                        {getFieldDecorator('email', {
                            initialValue: this.state.email
                        })(
                            <Input disabled />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="内容"
                        hasFeedback
                    >
                        {getFieldDecorator('content', {
                            initialValue: this.state.content
                        })(
                            <TextArea disabled autosize={{ minRows: 6 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="时间"
                    >
                        {getFieldDecorator('time', {
                            initialValue: this.state.time
                        })(
                            <Input disabled />
                        )}
                    </FormItem>
                    <FormItem
                        wrapperCol={{ span: 12, offset: 6 }}
                    >
                        <Button onClick={this.returnList} style={{"marginRight": "20px"}}>返回</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDataForm = Form.create({})(DataForm);

module.exports = WrappedDataForm;


